@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@400;600&display=swap);
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}
a {
  text-decoration: none;
}

body {
  font-family: "Rubik", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #3b3b3b;
  color: white;
  max-width: 1200px;
  margin: 0 auto;
}
a {
  color: lightgray;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.App {
  padding: 20px;
  text-align: left;
}


.MatchDetailsCard{
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding: 10px;
    border-radius: 10px;
    margin:10px;
    box-shadow: 5px 5px 5px 5px #3d3d3d;
}
.match-date{
    margin-top: 10px;
}
.match-venue{
    margin-top: 10px;
}
.match-result{
    margin-top: 10px;
}
.additional-detail{
    text-align: right;    
}

p{
    margin-bottom: 10px;
}
.won-card{
    background-color: rgb(69, 180, 138);
}
.lost-card{
    background-color: rgb(233, 91, 91)
}
.MatchSmallCard{
    padding: 10px;
    margin: 10px;
    border-radius: 10px;
    box-shadow: 5px 5px 5px 5px #3d3d3d;
}
.match-result{
    margin-top: 10px;
}

.won-card{
    background-color: rgb(69, 180, 138);
}

.lost-card{
    background-color: rgb(233, 91, 91);   
}
.TeamPage{
    display: grid;
    grid-template-columns:1fr 1fr 1fr 1fr;
    grid-template-rows:1fr 1fr 1fr;
    
    grid-column-gap: 10px;
    grid-row-gap: 10px;   
}
.team-name-section{
    grid-column: span 3;
    justify-self: start;
    align-self: end;
}
.team-name{
    font-size: 3rem;
    margin-bottom: 20px;
}
.win-loss-section{
    justify-self: center;
    padding: 40px;
    text-align: center;
}
.match-detail-section{
    grid-column: span 4;
}
.more-link{
    -webkit-align-self:center;
            align-self:center;
    justify-self: center;
    font-size:2rem;
}
.TeamTitle{
    display: -webkit-flex;
    display: flex;
    height: 170px;
    text-align: center;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    border: 1px solid white;
}
.HomePage{

}
.app-name{
    font-size: 3rem;
    margin:30px;
}

.team-grid{
    display:grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 10px;
    grid-row-gap: 10px;
}
.YearSelector{
    text-align: right;
    list-style: none;

}
li{
    margin-top:10px;
    margin-bottom: 10px;
}
.MatchPage{
    display: grid;
    grid-template-columns: 1fr 5fr;
    grid-column-gap: 10px;
}
.year-selector{
    justify-self: end;
    padding: 20px;
}
.page-heading{
    margin: 20px;
}

